	module.exports = {"data":{"wordpressPost":{"title":"The Korean Beauty Trend We Cannot Get Enough Of","content":"<p>Face masks are experiencing something of a comeback currently with a wide variety of types hitting beauty counters throughout the world. But you know that right? Heck &#8211; every woman and her grandmother are using them.<br />\nThe most popular are Korean-inspired sheet masks. If you’re a fan of Instagram you’ll already know this because you’ll have seen Hollywood stars and supermodels and regular beauty-loving folk like us posting selfies while sporting odd-looking masks of all kinds.<br />\nSo here’s the deal. They look incredibly strange, but the truth is they’re incredibly beneficial &#8211; and not just for boosting your social media likes.<br />\nYou see, this style of mask is soaked in skin-boosting ingredients. <strong>The idea is that you place the sheet mask on your face, lay back and let the ingredients absorb into your skin where they can do their thing.</strong><br />\nThe benefit here is two-fold. For starters, quality masks will be infused with highly-efficacious ingredients that have the power to actively change your skin. There are masks right now that can ease sun spots, increase moisture levels, smooth and soften. And they can do so in just 10 to 15 minutes too. All you need to do is choose the right one for you.<br />\nThe secondary benefit, sheet masks force you to take a moment to do nothing. And this is every bit as beneficial for your skin (and wellbeing) as the mask itself.<br />\nWe recommend you try one today. Go on. Take 10 to 15 minutes for yourself. And turn back the clock while you’re at it.</p>\n<p>We like<a href=\"https://www.sephora.com.au/products/caolion-pore-restoring-sheet-mask/v/1pc-30g\"> Caolion Pore Restoring Sheet Mask, $10</a>, and also <a href=\"http://mecca.com.au/mecca-cosmetica/lit-from-within-sheet-mask/I-024745.html#q=sheet%2Bmask%2B&amp;start=1\">Mecca Cosmetica Lit From Within Brightening Hydrogel Sheet Mask, $15.</a> Both bargains &#8211; both damn fine products.</p>\n<img class=\"alignnone size-medium wp-image-1681\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16183342/Mecca-JPG-300x180.jpg\" alt=\"\" width=\"300\" height=\"180\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16183342/Mecca-JPG-300x180.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16183342/Mecca-JPG-768x460.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16183342/Mecca-JPG.jpg 920w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><img class=\"alignnone size-medium wp-image-1682\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16183430/Where-is-pore-jpg-300x270.jpg\" alt=\"\" width=\"300\" height=\"270\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16183430/Where-is-pore-jpg-300x270.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16183430/Where-is-pore-jpg-768x691.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16183430/Where-is-pore-jpg.jpg 932w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>The ones we&#8217;re not so crazy about? Those emoji-styled ones that make you look like something out of a horror flick. We&#8217;re sorry &#8211; but why? Please. WHY?</p>\n<p>Here&#8217;s a terrifying pic for you, just in case you fancy seeing this cray cray trend in situ. Ta-dah.</p>\n<figure id=\"attachment_1685\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1685\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16190405/%40babymurr-JPG.jpg\" alt=\"\" width=\"300\" height=\"193\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16190405/%40babymurr-JPG.jpg 924w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16190405/%40babymurr-JPG-300x193.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16190405/%40babymurr-JPG-768x493.jpg 768w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">The emoji mask in action. It is um &#8230; not good. Image: Instagram/@babymurr</figcaption></figure>\n<figure id=\"attachment_1689\" style=\"width: 228px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1689\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16191115/mask-228x300.jpg\" alt=\"\" width=\"228\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16191115/mask-228x300.jpg 228w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16191115/mask.jpg 340w\" sizes=\"(max-width: 228px) 100vw, 228px\" /><figcaption class=\"wp-caption-text\">Admittedly they&#8217;re cute when in the packet.</figcaption></figure>\n<p>We&#8217;re not even going to tell you where to get them because well, no. Oh okay then &#8211; they&#8217;re made by the beauty brand <a href=\"https://www.petiteamieskincare.com/about\">Petite Amie Skincare</a>. You can learn more about the masks <a href=\"https://www.petiteamieskincare.com/emoji-masque\">here</a>. But if you do buy one, don&#8217;t visit us when you&#8217;re wearing it. They&#8217;re the stuff of nightmares.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/16182026/Drew-JPG.jpg"},"categories":[{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"04d1e0c6-e6b3-5970-b5cc-38cd3bbf9e37"}}

